import React from "react"
import Layout from "../components/Page"
import Domains from "src/containers/Domains"
import Word from "src/containers/Word"
import Search from "../components/Search"
import Header from "../components/Header"

class RootIndex extends React.Component {
  render() {
    if (typeof window === "undefined") {
      return (
        <Layout location={this.props.location}>
          <Header location={this.props.location} standalone={true} />
          {/*<Search*/}
          {/*  {...this.props}*/}
          {/*  className={"Search SearchHome"}*/}
          {/*  domains={false}*/}
          {/*  title=""*/}
          {/*  placeholder=""*/}
          {/*  home={true}*/}
          {/*  loading={true}*/}
          {/*/>*/}
        </Layout>
      )
    } else if (window.location.hostname.includes("thesaurus") || this.props.location.search.includes("thesaurus")) {
      return (
        <Layout location={this.props.location}>
          <Word location={this.props.location} />
        </Layout>
      )
    } else {
      return (
        <Layout location={this.props.location}>
          <Domains location={this.props.location} />
        </Layout>
      )
    }
  }
}

export default RootIndex

